"use client"

import { ReactNode, useState } from "react"
import { useCategory } from "@/contexts/Category"

export default function Page(): ReactNode {
    const [isOpen, setIsopen] = useState(false)
    const toggleDropdown = () => {
        setIsopen(!isOpen)
    }
    const { categoryList } = useCategory()
    return (
        <div>
            <p className="text-xl font-bold">Test page</p>

            <button
                id="dropdownDefaultButton"
                data-dropdown-toggle="dropdown"
                className="inline-flex items-center rounded-lg border bg-white px-5 py-2.5 text-center text-sm font-medium hover:bg-gray-300 focus:outline-none"
                type="button"
                onClick={toggleDropdown}
            >
                Dropdown button{" "}
                <svg
                    className="ms-3 h-2.5 w-2.5"
                    aria-hidden="true"
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 10 6"
                >
                    <path
                        stroke="currentColor"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth="2"
                        d="m1 1 4 4 4-4"
                    />
                </svg>
            </button>

            <div
                id="dropdown"
                className={
                    isOpen
                        ? "z-10 w-44 divide-y divide-gray-100 rounded-lg bg-white shadow"
                        : "hidden"
                }
            >
                <ul
                    className="py-2 text-sm text-gray-700 dark:text-gray-200"
                    aria-labelledby="dropdownDefaultButton"
                >
                    {categoryList.map((cat) => {
                        return (
                            <li key={cat.id}>
                                <button className="block w-full px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white">
                                    {cat.name}
                                </button>
                            </li>
                        )
                    })}
                </ul>
            </div>
        </div>
    )
}
